.education-header{
  font-family: 'Oxygen', sans-serif;
  text-decoration: underline;
  font-weight: 700;
  text-shadow: 2px 2px #83B4DC;
  text-align: center;
}
.education{
  font-family: 'Oxygen', sans-serif;
  padding:20px;
}
.education-one{
  display: inline-block;
  width: 35%;
  margin: 0 10% 0 10%;
 border-bottom: 2px solid #325C74;
 text-align: center;
}

.header-5{
  font-size: 25px;
}
@media (max-width: 768px){
  .education-one{
   margin-left: 220px;
   width: 50%;
   border-bottom: 2px solid #325C74;
  }
  .education-two{
    margin-left: 220px;
  }
  .education-header{
     margin-left:220px; 
     
  }
  
}
@media screen and (min-width:600px) and (max-width:1024px){
  .education-one{
   margin-left:100px;
   width: 50%;
   border-bottom: 2px solid #325C74;
  }
  .education-two{
    margin-left:100px;
  }
  .education-header{
     margin-left:100px; 
     
  }  
} 
